import { useRef, useEffect } from 'react';

/**
 * ref hookref
 *
 * @template T
 * @param value
 * @returns
 */
function useValueRef<T>(value: T) {
  const valueRef = useRef(value);

  useEffect(() => {
    valueRef.current = value;
  }, [value]);

  return valueRef;
}

export default useValueRef;
